import React, { useState } from 'react';
import { Form, Input, Tooltip, Row, Col, Checkbox, Button, message } from 'antd';
import { Link, useHistory } from "react-router-dom";
import { QuestionCircleOutlined } from '@ant-design/icons';
import axios from 'axios';

const formItemLayout = {
   labelCol: {
      xs: { span: 24 },
      sm: { span: 7 },
   },
   wrapperCol: {
      xs: { span: 24 },
      sm: { span: 17 },
   },
};
const tailFormItemLayout = {
   wrapperCol: {
      xs: {
         span: 24,
         offset: 0,
      },
      sm: {
         span: 17,
         offset: 7,
      },
   },
};
const tailFormItemLayout2 = {
   wrapperCol: {
      span: 24,
      offset: 0,
   },
};

const SignUp = ({ setInitialData, setPreload }) => {
   const [errors, setErrors] = useState(false);
	const [loading, setLoading] = useState(false);
	const [change, setChange] = useState(false);

   const [form] = Form.useForm();

   const history = useHistory();

   const onFinish = values => {
      setLoading(true);
      axios.post('/api/user/registration', values).then(({ data }) => {
			if (data.registerStatus === 200) {
            setPreload(true);
            setLoading(false);
				axios.post('/api/user/initial').then(({ data }) => {
		         setInitialData(data);
               setTimeout(() => {
                  history.push('/');
                  setPreload(false);
               }, 200);
		      });
			} else {
            setLoading(false);
            setChange(false);
            setLoading(false);
            setErrors(true);
            message.error('  !');
			}
      }).catch(() => {
         setLoading(false);
			message.error(' !   .');
		});
   };

  return (
   <div className="o-container o-wrapper">
      <Row justify="center" gutter={30} style={{ margin: "0" }}>
         <Col span={24} lg={18} xl={13}>
            <Form {...formItemLayout} form={form} name="register_form" onFinish={onFinish} scrollToFirstError className="c-form" onValuesChange={() => setChange(true)}>
               <Form.Item name="email" label="E-mail" rules={[ { type: 'email', message: '  E-mail!', }, { required: true, message: ' E-mail!', }, ]} hasFeedback>
                  <Input allowClear disabled={loading} />
               </Form.Item>

               <Form.Item name="username" label={
                  <span>
                     &nbsp;
                     <Tooltip title="  .">
                        <QuestionCircleOutlined />
                     </Tooltip>
                  </span>
               } rules={[{ required: true, message: ' !', whitespace: true }, {pattern: /^(?=[^-A-Z])(?=.*[a-z])[a-zA-Z0-9-_]{3,12}(?<![-])$/, message: '   3  12 ,      - ,    \'-\', \'_\'.       \'-\'.'} ]} hasFeedback className={ (errors && !change) && "ant-row ant-form-item ant-form-item-has-feedback ant-form-item-has-error" } >
                     <Input allowClear disabled={loading} />
               </Form.Item>

               <Form.Item name="fullname" label={<span>&rsquo;</span>} rules={[ { max: 20, message: "   20 - ." } ]} hasFeedback>
                     <Input allowClear disabled={loading} />
               </Form.Item>

               <Form.Item name="password" label="" rules={[ { required: true, whitespace: true, message: '  !', }, {pattern: /^([0-9a-zA-Z]){6,20}$/, message: '   6  20 ,     -   .'}, ]} hasFeedback>
                  <Input.Password disabled={loading} />
               </Form.Item>

               <Form.Item name="passwordConfirm" label=" " dependencies={[ 'password']} hasFeedback rules={[ { required: true, whitespace: true, message: '  !', }, ({ getFieldValue })=> ({ validator(rule, value) { if (!value || getFieldValue('password') === value) { return Promise.resolve(); } return Promise.reject('  !'); }, }), ]}>
                  <Input.Password disabled={loading} />
               </Form.Item>

               <Form.Item name="agreement" valuePropName="checked" rules={[ { validator:(_, value)=> value ? Promise.resolve() : Promise.reject('   !') }, ]} {...tailFormItemLayout}>
                  <Checkbox disabled={loading}>   </Checkbox>
               </Form.Item>

               <Form.Item {...tailFormItemLayout2}>
                  <Row justify="center">
                     <Col>
                        <Button disabled={loading} style={{ "fontSize": "24px", "height": "60px", "marginTop": "20px" }} size="large" type="primary" htmlType="submit">
                           
                        </Button>
                     </Col>
                  </Row>
               </Form.Item>
               <Row justify="center">
                  <Col>
                     <Link to="/login">  ?</Link>
                  </Col>
               </Row>
            </Form>
         </Col>
      </Row>
   </div>
  );
};

export default SignUp;
