import React from 'react';
import { Result, Button } from 'antd';
import { Link } from 'react-router-dom';

const Error404 = () => (
	<Result
		status="404"
		title="404"
		subTitle=",    ."
		extra={<Link to="/"><Button type="primary"> </Button></Link>}
   />)

export default Error404;
